<template>
  <div style="margin-top: 20px;margin-left:55px">
    <div>
      <span style="font-size: 40px">病人影像</span>
    </div>
    <div style="font-size: 25px;margin-top: 20px;display: flex" v-model="data">
      <div>
        <span>姓名：</span>
        <span>{{data.pname}}</span>
      </div>
      <div style="margin-left: 150px">
        <span>年龄：</span>
        <span>{{data.age}}</span>
      </div>
      <div style="margin-left: 150px">
        <span>性别：</span>
        <span>{{data.gender}}</span>
      </div>
      <div style="margin-left: 150px">
        <span>来源地：</span>
        <span>{{data.residence}}</span>
      </div>
    </div>
    <div style="margin-top: 30px;font-size: 20px" v-for="di in data.inquiries">
      <div>
        <span>{{di.date}}</span>
        <span>{{di.times}}</span>
      </div>
      <div v-for="image in di.images">
        <img style="height: 400px;width: 280px; margin-right: 20px" :src=didi v-for="didi in image.file">
      </div>
    </div>
    <div>
      <el-button @click="gotoLastPage" style="margin-left:45%;margin-bottom: 80px;margin-top: 50px;
      width: 100px;height: 60px;font-size: 25px;background-color:#20a0ff;border: solid 1px #20a0ff;border-radius:4px;color: white">返回</el-button>
    </div>
  </div>
</template>

<script>
    export default {
      name: "yxglpage",
      data(){
        return{
          data:{
            pname:"拉拉",
            gender:"男",
            residence:"四川省成都市",
            age:"23岁",
            inquiries:[{
              inquiryId:"",
              times:"第六次",
              date:"2016/02/13",
              images:[{
                imageId:"",
                file:[/*'../assets/img/zhongyi1.jpg',*/
                  "file:///" + "C:/Users/31490/Desktop/1111.png"],
              }
              ]
            },
              {
                inquiryId:"",
                times:"第七次",
                date:"2016/02/14",
                images:[{
                  imageId:"",
                  file:['../assets/img/zhongyi1.jpg',
                    '../assets/img/zhongyi1.jpg',
                    '../assets/img/zhongyi1.jpg'],
                }
                ]
              },
              {
                inquiryId:"",
                times:"第八次",
                date:"2016/02/15",
                images:[{
                  imageId:"",
                  file:['../assets/img/zhongyi1.jpg'],
                }
                ]
              }
            ],
          }
        }
      },
      methods:{
        gotoLastPage:function(){
          //跳转组件并且 传递pid
          var pathParams = window.localStorage.getItem("prePathParams");
          var prePathParams = window.localStorage.getItem("pathParams");
          prePathParams = JSON.parse(prePathParams);
          prePathParams.path = "brglpage";
          //缓存 目标跳转页面的参数
          this.$store.dispatch("setPathParams", pathParams);
          this.$store.dispatch("setPrePathParams", JSON.stringify(prePathParams));
          //缓存 跳转页面的参数
          this.$common.GotoPage(JSON.parse(pathParams).path,JSON.parse(pathParams),this);
        },
      }
    }
</script>

<style scoped>

</style>



